<template>
  <div>
    <input :width="width" type="checkbox" class="fine-checkbox" :id="name" name="happy" value="yes">
    <label :for="name"></label>
  </div>
</template>

<script>
export default {
  props: {
    width: {
      type: String,
      default: '10px'
    },
    name: {
      type: String,
      default: ''
    }
  }
}
</script>

<style lang="scss" scoped>

  .fine-checkbox {
    position: absolute;
    z-index: -1;
    opacity: 0;
  }

  .fine-checkbox+label {
    display: inline-flex;
    align-items: center;
    user-select: none;
  }

  .fine-checkbox+label::before {
    content: '';
    display: inline-block;
    width: 1em;
    height: 1em;
    flex-shrink: 0;
    flex-grow: 0;
    border: 1px solid #adb5bd;
    border-radius: 0.25em;
    margin-right: 0.5em;
    background-color: #fff;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50% 50%;
  }

  .fine-checkbox:checked+label::before {
    border-color: #0b76ef;
    background-color: #0b76ef;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
  }

  .fine-checkbox:not(:disabled):not(:checked)+label:hover::before {
    border-color: #b3d7ff;
  }

  .fine-checkbox:not(:disabled):active+label::before {
    background-color: #b3d7ff;
    border-color: #b3d7ff;
  }

  .fine-checkbox:focus+label::before {
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
  }

  .fine-checkbox:focus:not(:checked)+label::before {
    border-color: #80bdff;
  }

  .fine-checkbox:disabled+label::before {
    background-color: #e9ecef;
  }
</style>